.wrapper {
  max-width: 1246px;
  padding: 85px 15px 60px 15px;
  margin: 0 auto;
}
.category__list {
  gap: 0 60px;
  display: grid;
  grid-template-columns: repeat(3, 200px);
  text-align: center;
  justify-content: center;
  margin-bottom: 70px;
  @media (max-width: 842px) {
    gap: 0 10px;
  }
  @media (max-width: 630px) {
    display: flex;
    gap: 0 30px;
    justify-content: space-around;
  }
  @media (max-width: 478px) {
    flex-direction: column;
    gap: 20px 0;
  }
  li {
    color: #3e424b;
    font-family: "Roboto", sans-serif;
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: 115%;
    transition: 0.3s ease;
    position: relative;
    cursor: pointer;
    &:hover {
      color: #bb8c5f;
    }
    @media (max-width: 842px) {
      font-size: 20px;
    }
  }
}
.goods {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  gap: 20px;
  margin-bottom: 50px;
}
.moreBtn {
  background-image: url("../../../public/categoryGoods/showMoreBtn.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: transparent;
  color: #bb8c5f;
  text-align: center;
  font-family: Jost, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  padding: 32px 0;
  display: block;
  margin: 0 auto;
  transition-duration: 0.3s;
  width: 370px;
  &:hover {
    opacity: 0.8;
  }
}
.noGoods {
  display: inline-block;
  margin: 0 auto;
  font-size: 30px;
  font-weight: 500;
  justify-content: center;
  font-family: "Roboto", sans-serif;
}
.moreBtnClose {
  background-image: url("../../../public/categoryGoods/showMoreBtn.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: transparent;
  color: #bb8c5f;
  text-align: center;
  font-family: Jost, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  // padding: 26px 112px 24px;
  padding: 32px 40px;
  width: 370px;
  display: block;
  margin: 0 auto;
  transition-duration: 0.3s;
  &:hover {
    opacity: 0.8;
  }
}
.category__list .category__active {
  color: #bb8c5f;
}
.carousel {
  display: none;
  @media (max-width: 1248px) {
    display: block;
  }
}
@media (max-width: 1248px) {
  .moreBtn,
  .moreBtnClose {
    display: none;
  }
  .goods {
    display: none;
  }
  :global {
    .swiper {
      min-height: 495px;
    }
    .swiper-slide {
      display: flex !important;
      justify-content: center !important;
      @media (max-width: 636px) {
        .swiper-slide {
          width: 620px !important;
        }
      }
    }

    .swiper-pagination {
      display: flex;
      gap: 0 15px;
      justify-content: center;
    }
    .swiper-pagination-bullet {
      border: 1px solid #524336;
      width: 15px !important;
      height: 15px !important;
      border-radius: 0 !important;
      background: transparent !important;
      opacity: 1 !important;
      position: relative !important;
    }
    .swiper-pagination-bullet-active {
      background-color: #bb8c5f !important;
    }
  }
}
@media (max-width: 768px) {
  .wrapper {
    padding: 60px 15px 60px 15px;
  }
}
